.dp-toolbar-settings-page {
	#wpcontent {
		padding-left: 0;
	}
}

.dp-admin-page-wrap {
	display: flex;
	flex-direction: column;
}

.dp-admin-page-header {
	background: #fff;
	border-bottom: 1px solid #e0e0e0;
	color: #1e1e1e;

	&-container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 60px;
	}
}

@media screen and (min-width: 783px) {}

.dp-admin-page-title {
	font-size: 23px;
	font-weight: 400;
	margin: 0;
	padding: 0 20px;
}

.dp-admin-page-body {
	flex: 1;
}

.dp-admin-page-content {
	max-width: 640px;
	padding: 1.5rem;
	margin: 0 auto;

	* {
		box-sizing: border-box;
	}
}

.dp-admin-page-save-settings {
	text-align: center;
}

.dp-toolbar-display-rule {
	margin-left: 32px;
}

.dp-toolbar-roles-control,
.dp-toolbar-caps-control {
	// margin-left:32px;
}

.dp-toolbar-roles-control {
	>label {
		display: block;
		margin-bottom: 6px;
	}
}

.dp-toolbar-roles-option-list {
	// padding-left:32px;
}

.dp-toolbar-settings-notices {
	position: fixed;
	bottom: 20px;
	top: auto;
	left: auto;
	right: 20px;
	display: inline-flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-end;
}

